.conTop {
    max-width: 1240px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 32px;
    margin: 32px auto;
}

.form-container {
    background-color: #2c2f39;
    border-radius: 24px;
    text-align: center;
    max-width: 473px;
}

.form-title {
    font-size: 32px;
    margin-bottom: 24px;
    color: #fff;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.form label {
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    text-align: left;
}

.formcon {
    max-width: 400px;
    margin: 32px;
}

.form input,
.form textarea {
    width: 100%;
    padding: 10px;
    border: 2px solid rgba(251, 178, 22, 1);
    border-radius: 5px;
    background-color: #f7f7f7;
    color: #333;
    transition: border-color 0.3s;
    margin-top: 8px;
}

.form input::placeholder,
.form textarea::placeholder {
    color: #bbb;
}

.form input:focus,
.form textarea:focus {
    border-color: #ffa500;
    box-shadow: 0 0 5px rgba(255, 165, 0, 0.5);
}

.form textarea {
    resize: none;
    height: 100px;
}

.form-button {
    padding: 12px 155px;
    font-size: 20px;
    background-color: rgba(251, 178, 22, 1);
    color: #000;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.form-button:hover {
    background-color: #ff8c00;
}

.www {
    max-width: 750px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mainInfCon {
    background-color: rgba(218, 224, 229, 1);
    border-radius: 24px;
    padding: 24px 32px;
}

.cotacts-inf-con {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 735px;
}

.highlight {
    font-size: 20px;
}

.inf {
    font-size: 24px;
}

.inf23 {
    font-size: 32px;
    line-height: 38px;
}

.contact-section {
    max-width: 313px;
}

.infp {
    font-size: 18px;
    line-height: 25px;
}

.centr {
    max-width: 1244px;
    margin: auto;
}

.h32centr {
    display: flex;
    justify-content: center;
    font-size: 48px;
    margin-top: 80px;
}

.map {
    display: grid;
    grid-template-columns: 3fr 1fr;
    max-width: 1240px;
    margin: auto;
}

.mainMap {
    width: 822px;
    height: 509px;
    border-radius: 16px;
    border: 1px solid rgba(10, 10, 10, 1);
}