.footer {
    background-color: rgba(39, 46, 54, 1);
    color: #fff;
    padding: 80px;
    max-width: 1240px;
    max-height: 567px;
    margin: 100px auto;
    border-radius: 32px;
}

.footer__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.footer__about,
.footer__equipment,
.footer__contacts {
    flex: 1;
    min-width: 257px;
}

.footer__logo {
    max-width: 248px;
    margin-bottom: 32px;
}

.footer__description {
    margin-bottom: 36px;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
}

.footer__social {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.footer__social_link {
    background-color: rgba(251, 178, 22, 1);
    border-radius: 50%;
    padding: 15px;
    width: 26px;
    height: 26px;
}

.footer__social_link img {}

.footer__heading {
    font-size: 18px;
    margin-bottom: 16px;
    font-weight: bold;
}

.footer__list {
    list-style: none;
    padding: 0;
}

.footer__list li {
    margin-bottom: 12px;
    font-size: 16px;
}

.footer__address {
    margin-top: 10px;
    font-size: 16px;
    line-height: 1.5;
}

.footer__bottom {
    text-align: center;
    margin-top: 12px;
    font-size: 16px;
    border-top: 1px solid rgba(255, 253, 253, 1);
    padding-top: 10px;
}

.footer__about {
    margin-right: 132px;
}

.footer__services {
    max-width: 187px;
    margin-left: 19px;
    margin-right: 33px;
}

.language_phone_container__phone {
    color: #fbb216;
    font-size: 20px;
    font-weight: 600;
}