header {
    height: 104px;
    box-sizing: border-box;
    background-color: #272e36;
    border-radius: 24px;
    padding: 26px 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    margin: 16px 0;
}

nav {
    display: flex;
    color: #fffdfd;
    gap: 32px;
}

header .right-side-of-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
}

header .language-phone-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

header .language-phone-container p {
    margin: 0;
    cursor: pointer;
}

header .language-phone-container__language-selector {
    display: flex;
    color: #fffdfd;
    margin-bottom: 6px;
}

header .language-phone-container__language-selector hr {
    border: 1px solid #ccc;
    height: 18px;
    margin: auto 6px;
    opacity: 0.5;
}

header .language-phone-container__phone {
    color: #fbb216;
    font-size: 20px;
    font-weight: 600;
}

a {
    text-decoration: none;
    cursor: pointer;
    color: white;
}